.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  font-size: 1.5em;
  margin: 1em 0;
}

input[type="date"] {
  font-size: 1em;
  padding: 0.5em;
  border: 2px solid #000;
  border-radius: 3px;
}

input[type="submit"] {
  font-size: 1em;
  padding: 0.5em 1em;
  border: 2px solid #000;
  border-radius: 3px;
  background-color: white;
  color: #000;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #000;
  color: white;
}

p {
  font-size: 1.5em;
}
